.avatar i {
	font-size: 125%;
	vertical-align: sub;
}
.avatar-status {
	position: absolute;
	right: -2px;
	bottom: -2px;
	width: .75rem;
	height: .75rem;
	border: 2px solid #fff;
	border-radius: 50%;
}
.avatar-sm {
	width: 1.6rem;
	height: 1.6rem;
	line-height: 1.6rem;
	font-size: .75rem;
}
.avatar-md {
	width: 2.5rem;
	height: 2.5rem;
	line-height: 2.5rem;
	font-size: 1rem;
}
.avatar-lg {
	width: 3rem;
	height: 3rem;
	line-height: 3rem;
	font-size: 1.25rem;
}
.avatar-xl {
	width: 4rem;
	height: 4rem;
	line-height: 4rem;
	font-size: 1.75rem;
}
.avatar-xxl {
	width: 5rem  !important;
	height: 5rem !important;
	line-height: 5rem;
	font-size: 2rem;
}
.avatar-placeholder {
	background: #ced4da url('data:image/svg+xml;charset=utf8,<svg xmlns="http://www.w3.org/2000/svg" width="134" height="134" viewBox="0 0 134 134"><path fill="#868e96" d="M65.92 66.34h2.16c14.802.42 30.928 6.062 29.283 20.35l-1.618 13.32c-.844 6.815-5.208 7.828-13.972 7.866H52.23c-8.764-.038-13.13-1.05-13.973-7.865l-1.62-13.32C34.994 72.4 51.12 66.76 65.92 66.34zM49.432 43.934c0-9.82 7.99-17.81 17.807-17.81 9.82 0 17.81 7.99 17.81 17.81 0 9.82-7.99 17.807-17.81 17.807-9.82 0-17.808-7.987-17.808-17.806z"/></svg>') no-repeat (center / 80%);
}
.avatar-list {
	margin: 0 0 -.5rem;
	padding: 0;
	font-size: 0;
	.avatar {
		margin-bottom: .5rem;
		&:not(:last-child) {
			margin-right: .5rem;
		}
	}
}
.avatar-list-stacked .avatar {
	margin-right: -.8em !important;
	box-shadow: 0 0 0 2px #fff;
}
.avatar-blue {
	background-color: #c8d9f1;
	color: #467fcf;
}
.avatar-indigo {
	background-color: #d1d5f0;
	color: #6574cd;
}
.avatar-purple {
	background-color: #e4cff9;
	color: #867efc;
}
.avatar-pink {
	background-color: #fcd3e1;
	color: #ec82ef;
}
.avatar-red {
	background-color: #f0bcbc;
	color: #c21a1a;
}
.avatar-orange {
	background-color: #fee0c7;
	color: #fc7303;
}
.avatar-yellow {
	background-color: #fbedb7;
	color: #ecb403;
}
.avatar-green {
	background-color: #cfeab3;
	color: #4ecc48;
}
.avatar-teal {
	background-color: #bfefea;
	color: #2bcbba;
}
.avatar-cyan {
	background-color: #b9e3ea;
	color: #17a2b8;
}
.avatar-white {
	background-color: white;
	color: #fff;
}
.avatar-gray {
	background-color: #dbdde0;
	color: #868e96;
}
.avatar-gray-dark {
	background-color: #c2c4c6;
	color: #343a40;
}
.avatar-azure {
	background-color: #c7e6fb;
	color: $info;
}
.avatar-lime {
	background-color: #d7f2c2;
	color: #7bd235;
}
.img-avatar {
	height: 37px;
	border-radius: 2px;
	width: 37px;
}
.avatar-group {
	.avatar {
		position: relative;
		z-index: 2;
		background: transparent !important;
		&:hover {
			z-index: 3;
		}
		+.avatar {
			margin-left: -0.8rem;
		}
	}
	.avatar-sm {
		font-size: .875rem;
		width: 32px;
		height: 32px;
	}
}
#basicwizard .nav-tabs .nav-link {
	padding: .80rem .80rem;
}
.avatar-border {
	width: 3.4rem;
	height: 3.4rem;
	position: absolute;
	border: 3px solid $primary;
	left: -7px;
	right: 0;
	line-height: 3.4rem;
	border-radius: 50%;
	text-align: center;
	top: -7px;
	background: transparent;
}
.app-sidebar__user .avatar-status {
	position: absolute;
	right: -5px;
	bottom: -2px;
	width: .75rem;
	z-index: 1;
	height: .75rem;
	border: 2px solid #fff;
	border-radius: 50%;
	background: #fff;
}
.avatar {
	width: 2rem ;
	height: 2rem ;
	line-height: 2rem;
	display: inline-block;
	position: relative;
	text-align: center;
	color: #fff;
	font-weight: 500;
	vertical-align: bottom;
	font-size: .875rem;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.avatar-sm {
    width: 1.6rem;
    height: 1.6rem;
    line-height: 1.6rem;
    font-size: .75rem;
}
.avatar-md {
    width: 2.5rem;
    height: 2.5rem;
    line-height: 2.5rem;
    font-size: 1rem;
}
.avatar-lg {
    width: 3rem;
    height: 3rem;
    line-height: 3rem;
    font-size: 1.25rem;
}
.avatar-xl {
    width: 4rem;
    height: 4rem;
    line-height: 4rem;
    font-size: 1.75rem;
}